.custom-dialog {
  position: absolute;
  width: 30%;
}
.custom-dialog #custom-dialog-content {
    position: absolute;
    width: 100%;
    background: #fff;
    -webkit-box-shadow: 0 4px 6px rgba(10, 16, 20, 0.15), 0 0 12px rgba(10, 16, 20, 0.12);
            box-shadow: 0 4px 6px rgba(10, 16, 20, 0.15), 0 0 12px rgba(10, 16, 20, 0.12);
    border-radius: 4px;
    padding: 20px 30px;
    -webkit-animation: custom-dialog-in .3s ease-out;
            animation: custom-dialog-in .3s ease-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    opacity: 0;
    left: 50%;
    top: 50%;
    z-index: 999;
}
.custom-dialog #custom-dialog-content > img {
      cursor: auto;
}
.custom-dialog #custom-dialog-content i.el-icon-close {
      position: absolute;
      top: 10px;
      right: 10px;
      padding: 5px;
      cursor: pointer;
}
@-webkit-keyframes custom-dialog-in {
from {
    opacity: 0.3;
}
to {
    opacity: 1;
}
}
@keyframes custom-dialog-in {
from {
    opacity: 0.3;
}
to {
    opacity: 1;
}
}

.mask-dialog {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 100;
  display: none;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  color: white;
}
.mask-dialog.show {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.mask-dialog #mask-dialog-content {
    -webkit-animation: fadeIn 0.6s;
            animation: fadeIn 0.6s;
    max-height: 90%;
}
.mask-dialog #mask-dialog-content > img {
      cursor: auto;
}
.mask {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  background: #000;
  z-index: 99;
  -webkit-animation: mask-fade-in 0.4s;
          animation: mask-fade-in 0.4s;
}
@-webkit-keyframes mask-fade-in {
from {
    opacity: 0;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
}
@keyframes mask-fade-in {
from {
    opacity: 0;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
}

#empty-content #load {
  width: 50px;
  -webkit-animation: loading 3s linear infinite;
          animation: loading 3s linear infinite;
}
#empty-content #load #loading-inner {
    stroke-dashoffset: 0;
    stroke-dasharray: 300;
    stroke-width: 10;
    stroke-miterlimit: 10;
    stroke-linecap: round;
    -webkit-animation: loading-circle 2s linear infinite;
            animation: loading-circle 2s linear infinite;
    stroke: #727575;
    fill: transparent;
}
@-webkit-keyframes loading {
0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes loading {
0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@-webkit-keyframes loading-circle {
0% {
    stroke-dashoffset: 0;
}
100% {
    stroke-dashoffset: -600;
}
}
@keyframes loading-circle {
0% {
    stroke-dashoffset: 0;
}
100% {
    stroke-dashoffset: -600;
}
}












